// Navigation Bar Styles
.Nav {
    background: #0B0C10;
    height: 80px;
    display: flex;
    justify-content: space-between;
    padding: 0.5rem calc((100vw - 1000px) / 2);
    z-index: 10;
    position: sticky;
    top: 0;
    font-family: Montserrat;
}

.NavLink {
   
    background: #0B0C10;
    color: #C5C6C7;
    display: flex;
    font-weight: 500;
    align-items: center;
    text-decoration: none;
    list-style: none;
    padding: 0 1rem;
    height: 100%;
    cursor: pointer;

    &:hover{
        color: white;

        &:active{
            color: #66FCF1;
        }
    }

}
.Bars {
    display: none;
    color:#C5C6C7;

    @media screen and (max-width: 768px){
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        transform: translate(-100%, 75%);
        font-size: 1.8rem;
        cursor: pointer;
    }
}

.NavMenu {
    display: flex;
    align-items: center;
    margin-right: -24px;

    @media  screen and (max-width: 768px)  {
        display: none;
    }
}

.NavBtn{
    display: flex;
    align-items: center;
    margin-right: 24px;

    @media screen and (max-width: 768px) {
        display: none;
    }
}

.NavBtnLink{
    border-radius: 4px;
    background: #45A29E;
    padding: 10px 20px;
    font-weight: bolder;
    color: #0B0C10;
    outline: none;
    cursor: pointer;
    transition: all 0.2s ease-in-out;
    text-decoration: none;

    &:hover{
        transition: all 0.2s ease-in-out;
        background: #C5C6C7;
        color: #010606
    }
}

a{text-decoration: none;}

